<script>
    import {modalStates, programState} from './stores'
    import {showFragmentModal} from './modals_visibility';
    import FragmentModal from "./FragmentModal.svelte";
    import ModifierModal from "./ModifiersModal.svelte";
    import Fragment from './Fragment.svelte'

    let fragmentModalVisibility
    let modifierModalVisibility;

    let modals = modalStates.subscribe(state => {
        fragmentModalVisibility = state.fragmentModalVisibility;
        modifierModalVisibility = state.modifierModalVisibility;
    })

</script>

<div class="container">
    <div class="row">
        <label>Program
            <select bind:value={$programState.type} class="custom-select">
                <option disabled value>Choose...</option>
                <option value="solo">Solo</option>
                <option value="duet">Duet</option>
                <option value="team">Team</option>
            </select>
        </label>
        <label>Name
            <input bind:value={$programState.name} class="form-control" type="text">
        </label>
    </div>
    <div class="row">
        <div>
            <label>Duration
                <input bind:value={$programState.duration} class="form-control" disabled type="time">
            </label>
        </div>
        <div>
            <label>Difficulty
                <input bind:value={$programState.mark} class="form-control" disabled type="text">
            </label>
        </div>
    </div>
    {#if $programState.type === "team"}
        <div>
            <div class="form-check">
                <input id="team-unique" type="checkbox" class="form-check-input">
                <label for="team-unique" class="form-check-label">Hight level difficulty pattern
                    changes
                    (moving patterns, passing through patterns, close spaced, quick and sharp
                    surface changes)
                </label>
            </div>
        </div>
    {/if}
    {#if $programState.type}
        <div class="row">
            <button class="btn btn-outline-primary">Get .asd file</button>
            <button class="btn btn-outline-primary ">Get .pdf file</button>
        </div>
        <div class="row-flex">
            <button on:click="{showFragmentModal}" class="btn btn-primary">Create new
                fragment
            </button>
        </div>
    {/if}
    <div class='fragments'>
        {#each $programState.fragments as fragment,id}
            <Fragment>
                <div slot="header">
                    #{id+1}
                </div>
                <div slot="header">
                    {fragment.duration} sec.
                </div>
                <div slot="body">
                    {fragment.type}
                </div>
                <div slot="footer">
                    Diff.: {fragment.difficulty}
                </div>
            </Fragment>
        {:else}
            <div>
                Program fragments will be there.
            </div>
        {/each}
    </div>
</div>

{#if fragmentModalVisibility}
    <FragmentModal/>
{/if}
{#if modifierModalVisibility}
    <ModifierModal/>
{/if}

<style>
    input:disabled{
        color:black;
        background-color: deepskyblue;
        font-weight: 800;
    }
    div{
        text-transform: inherit;
        text-align: inherit;
        font-weight: inherit;
    }

    .container {
        width: 40%;
        margin-right: auto;
        margin-left: auto;
    }

    .fragments {
        display: grid;
        grid-template-rows: repeat(3, 1fr);
        grid-template-columns: repeat(3, 1fr);
    }

    .row {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 10px;
    }

    .row-flex {
        display: flex;
        flex-direction: column;
    }
</style>