<script>
    import {onMount, onDestroy, setContext} from 'svelte'
    import {get} from 'svelte/store'
    import {modalStates,fragmentState,programState} from './stores'
    import {showModifierModal, hideFragmentModal, showFragmentModal} from './modals_visibility';
    import {calculateFragmentBasicMark, calculateFragmentDifficulty, calculateProgramDifficulty, calculateProgramTime} from './calculations'
    import Modal from './Modal.svelte'
    import Modifier from './Modifier.svelte'

    let fragment = get(fragmentState);
    let fragmentModalVisibility
    let modifierModalVisibility;
    let inputType;
    let inputDuration;
    let inputMovements;
    let inputLegs;

    onMount( ()=> {
        $fragmentState.duration = null;
        $fragmentState.numberOfMovements = null
        $fragmentState.legs = 1;
        $fragmentState.basicMark = 0;
        $fragmentState.modifiers = [];
    })


    let modals = modalStates.subscribe(state => {
        fragmentModalVisibility = state.fragmentModalVisibility;
        modifierModalVisibility = state.modifierModalVisibility;
    })

    const saveFragment = function () {
        let fragment = get(fragmentState);
        let program = get(programState)
        fragment.basicMark = calculateFragmentBasicMark(fragment,program.type)
        fragment.difficulty = calculateFragmentDifficulty(fragment,program.type);
        $programState.fragments = [...program.fragments, Object.assign({},fragment)];
        $programState.mark = calculateProgramDifficulty($programState)
        $programState.duration = calculateProgramTime($programState)
        hideFragmentModal();
    }
    const addModifier = function(){
        if(     (inputType && inputType.checkValidity()) &&
                (inputDuration && inputDuration.checkValidity()) &&
                (!inputMovements || inputMovements.checkValidity()) &&
                (!inputLegs || inputLegs.checkValidity())
        ){
            showModifierModal()
        }
    }
    const removeModifier = function (modifier) {
        let index = $fragmentState.modifiers.findIndex(el=>el===modifier);
        $fragmentState.modifiers= [...$fragmentState.modifiers.slice(0, index), ...$fragmentState.modifiers.slice(index + 1)]
    }
    const rerenderModal = function () {
        $fragmentState.duration = null;
        $fragmentState.numberOfMovements = null
        $fragmentState.legs = null;
        $fragmentState.basicMark = 0;
        $fragmentState.modifiers = [];
    }
</script>

<Modal on:close="{hideFragmentModal}" z_index=1>
    <div  slot="header">
        <div class="col">
            <h2>New Fragment</h2>
        </div>
    </div>
    <div slot="body" class="container">
        <div class="form">
            <label>Type
                <select required on:change={rerenderModal} bind:this={inputType} bind:value={$fragmentState.type} class="custom-select">
                    <option value="hybrid">Hybrid</option>
                    <option value="transition">Transition</option>
                </select>
            </label>
            <label>Duration (sec)
                <input required bind:this={inputDuration} bind:value={$fragmentState.duration} type="number" class="form-control">
            </label>

        {#if $fragmentState.type === "hybrid"}
                <label>Number of Movements
                    <input required bind:this={inputMovements} bind:value={$fragmentState.numberOfMovements} class='form-control' type="number">
                </label>
                <label>Legs
                    <select required bind:this={inputLegs} bind:value={$fragmentState.legs} class="custom-select">
                        <option value="1">One leg (raising or lifting)</option>
                        <option value="2">Two legs (raising or lifting)</option>
                    </select>
                </label>
        {/if}
        <button on:click="{addModifier}" class="btn btn-outline-primary">Add modifier</button>
        </div>
        <div class="modifiers">
            {#each $fragmentState.modifiers as modifier}
                <Modifier on:click={()=>removeModifier(modifier)}>
                    <div slot="header" class="modifier-header">
                        {modifier["category"]}
                    </div>
                    <div slot="body" class="modifier-body">
                        {modifier.name}
                    </div>
                    <div slot="footer" class="modifier-footer">
                        {#if $fragmentState.type === 'hybrid'}
                            {modifier["category"]}: {modifier["values"][$programState.type]}
                        {:else}
                            Modifier: {modifier["values"][$programState.type]}
                        {/if}
                    </div>
                </Modifier>
            {:else}
                <div>
                    Your fragment modifiers will be there.
                </div>
            {/each}
        </div>
    </div>
    <div slot="footer" class="footer">
        <button on:click="{saveFragment}" class="btn btn-primary">Save</button>
        <button on:click="{hideFragmentModal}" class="btn btn-danger">Dismiss</button>
    </div>
</Modal>


<style>
    .container{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .footer{
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
    }
    button {
        display: block;
        margin-left: 5px;
        margin-right: 5px;
    }

    .modifiers{
        display: grid;
        grid-template-rows: repeat(3, 1fr);
        grid-template-columns: repeat(4, 1fr);
    }
    .modifier-header {
        padding: 5px;
        background-color: rgba(0, 0, 0, .03);
        border-top: 1px solid rgba(0, 0, 0, .125);
    }

    .modifier-body {
        font-weight: bolder;
        flex: 1 1 auto;
        min-height: 1px;
        padding: 5px;
    }

    .modifier-footer {
        padding: 5px;
        background-color: rgba(0, 0, 0, .03);
        border-top: 1px solid rgba(0, 0, 0, .125);
    }
</style>
